import React, { useRef } from 'react'
import {
  Button,
  ButtonStatus,
  Divider,
  Icon,
  RadioSelector,
  Text,
  Title
} from '../../../../../atoms'
import { InputAdornment } from '../../../../../molecules'
import {
  Column,
  Container,
  Row,
  Spacer
} from '../../../../../layout'
import { getGlobalStyle } from '../../../../../../helpers'
import { toCapitalize } from '../../../../../../utils'
import { ManualAddress } from '../../../ModalState/AddressModal/ManualAddress'
import { AddressListProps } from '../../../StoreLocator'
import styles from '../../AddressModal/index.module.css'

export interface UserAddressListProps {
  addressName?: string;
  addressType?: string;
  city?: string;
  complement?: string;
  country?: string;
  data?: UserAddressListProps
  geoCoordinate?: number[];
  id?: string;
  index?: number;
  neighborhood?: string;
  number?: string;
  postalCode?: string;
  receiverName?: string;
  reference?: string;
  selectable?: boolean;
  selected?: boolean;
  site?: 'unimarc' | 'alvi';
  state?: string;
  street?: string;
  addressSelector: (data: object, index: number) => void;
}
interface AddressModalProps {
  addressList: AddressListProps[];
  confirmAddressButtonStatus: ButtonStatus;
  isMobile?: boolean,
  manualAddress?: boolean;
  userAddressList: UserAddressListProps[];
  value?: string,
  addressOnChangeSearch?: (e?: React.ChangeEvent<HTMLInputElement>) => void;
  addressOnClearInput?: (e?: React.ChangeEvent<HTMLInputElement>, refAdornm?: unknown) => void;
  addressSelector: (data: object, index: number) => void;
  changeDeliveryMethod: () => void;
  confirmUserAddress: () => void;
  deleteAddress?: (data: object, index: number) => void;
  onClick?: () => void;
  onSelect?: (data) => void;
  onSelectManualAddress?: () => void;
}

export const AddressModalContent = ({
  addressList,
  confirmAddressButtonStatus,
  isMobile = false,
  manualAddress = false,
  userAddressList,
  value,
  addressOnChangeSearch,
  addressOnClearInput,
  addressSelector,
  changeDeliveryMethod,
  confirmUserAddress,
  onClick,
  onSelect,
  onSelectManualAddress
}: AddressModalProps): React.ReactElement => {
  const inputAdornmentRef = useRef<HTMLDivElement>(null)
  const onClickClearInput = (e?: React.ChangeEvent<HTMLInputElement>) => {
    addressOnClearInput(e, inputAdornmentRef)
  }

  return (
    <>
      <Container>
        <Column>
          {isMobile && (
            <Spacer.Horizontal size={16} />
          )}
          <Row justifyContent={isMobile ? 'center' : 'start'}>
            <Title
              color={getGlobalStyle('--color-icons-black')}
              customFontSize={getGlobalStyle(`--font-size-${isMobile ? 'lg' : '2xl'}`)}
              fontWeight='semibold'
              headingLevel='h5'
              text='¿Dónde quieres recibir tu compra?'
              textAlign='center'
            />
          </Row>
          {!isMobile && (
            <Container
              absoluteDefault='topRight'
              left='444px'
              maxWidth='40px'
              padding='24px 0px'
              position='absolute'
            >
              <Icon
                clickable='pointer'
                color={getGlobalStyle('--color-icons-black')}
                customSize={30}
                name='Close2'
                onClick={onClick}
              />
            </Container>
          )}
          {!isMobile && (
            <>
              <Spacer.Horizontal size={8} />
              <Divider
                color={getGlobalStyle('--color-neutral-gray')}
                thickness={1}
                verticalMargin={0}
              />
            </>
          )}
          <Spacer.Horizontal size={24} />
          <Row>
            <Text
              fontSize='lg'
              fontWeight='semibold'
              textAlign='center'
            >
              Agrega o escoge una dirección
            </Text>
          </Row>
          <Spacer.Horizontal size={16} />
          <InputAdornment
            background={getGlobalStyle('--color-background-white')}
            border={`1px solid ${getGlobalStyle('--color-icons-black')}`}
            borderRadius={getGlobalStyle('--border-radius-xl')}
            fontSize='md'
            inputAdornmentEnd={
              value ? (
                <Icon
                  clickable='pointer'
                  color={getGlobalStyle('--color-icons-gray')}
                  customSize={12}
                  name='Close'
                  onClick={onClickClearInput}
                />
              ) : (
                <Icon
                  clickable='pointer'
                  color={getGlobalStyle('--color-icons-gray')}
                  customSize={18}
                  name='SearchCart'
                  onClick={onClickClearInput}
                />
              )
            }
            inputWidth='100%'
            onChange={addressOnChangeSearch}
            onFocus={null}
            padding='12.5px 15px'
            placeholder='Ingresa una nueva dirección...'
            placeholderFocus='placeholder'
            ref={inputAdornmentRef}
            width='auto'
          />
          <Spacer.Horizontal size={12} />
          <Row justifyContent='start'>
            <Container margin='0px 2px 0px 0px' width='fit-content'>
              <Icon customSize={20} name='Location2'/>
            </Container>
            <Text customColor={getGlobalStyle('--color-neutral-gray-dark')} fontSize='md'>
              ¿No encuentras tu dirección?
            </Text>
            <Text
              customClassName={styles.underlineText}
              fontSize='md'
              onClick={onSelectManualAddress}
            > Ingrésala aquí </Text>
          </Row>
          <Spacer.Horizontal size={12} />
        </Column>
      </Container>
      {addressList.length > 0 &&
        <>
          <Container maxHeight={isMobile ? '70vh' : '310px'} overflow='auto'>
            <Column>
              {
                addressList.map((data, i) => {
                  return (
                    <Row
                      clickable='pointer'
                      key={i}
                      onClick={() => {
                        onSelect(data)
                      }}
                    >
                      <Column
                        clickable='pointer'
                        padding='10px 10px 0px 0px'
                        width='fit-content'
                      >
                        <Icon
                          clickable='pointer'
                          customSize={42}
                          name='LocationInCircle'
                        />
                      </Column>
                      <Column>
                        <Spacer.Horizontal size={12} />
                        <Text
                          clickable='pointer'
                          color='black'
                          fontSize='md'
                          fontWeight='semibold'
                          isFlex
                          textAlign='left'
                        >
                          {data.name}
                        </Text>
                        <Spacer.Horizontal size={2} />
                        <Text
                          clickable='pointer'
                          color='gray'
                          fontSize='md'
                          fontWeight='regular'
                          isFlex
                          textAlign='left'
                        >
                          {data.city}
                        </Text>
                        <Spacer.Horizontal size={12} />
                      </Column>
                    </Row>
                  )
                })
              }
            </Column>
          </Container>
          {
            manualAddress &&
            <ManualAddress />

          }
        </>
      }

      {addressList.length === 0 && userAddressList.length > 0 &&
        <>
          <Spacer.Horizontal size={12} />
          <Row>
            <Text fontSize='lg' fontWeight='semibold'>
              Direcciones guardadas
            </Text>
          </Row>
          <Spacer.Horizontal size={8} />
          <Column
            customHeight={isMobile ? null : '160px'}
            overflow='auto'
            padding='0px -20px'
          >
            {
              userAddressList.map((data, i) => {
                return (
                  <Container
                    alignItems='center'
                    clickable='pointer'
                    key={i}
                  >
                    <RadioSelector checked={data.selected} onClick={() => { return addressSelector(data, i) }} />
                    <Column margin='0px 0px 0px 10px' onClick={() => { return addressSelector(data, i) }}>
                      <Spacer.Horizontal size={12} />
                      <Text
                        clickable='pointer'
                        color='black'
                        fontSize='md'
                        fontWeight='semibold'
                        isFlex
                        textAlign='left'
                      >
                        {data.street.length > 0 ? data.street + ',' : ''} {data.number}
                      </Text>
                      <Spacer.Horizontal size={2} />
                      <Text
                        clickable='pointer'
                        color='gray'
                        fontSize='md'
                        fontWeight='regular'
                        isFlex
                        textAlign='left'
                        truncate={60}
                      >
                        {data.neighborhood + ', ' + toCapitalize(data.state.toLocaleLowerCase())}
                      </Text>
                      <Spacer.Horizontal size={8} />
                    </Column>
                  </Container>
                )
              })
            }
          </Column>
          <Column
            className={styles.spacerShadow}
            margin={isMobile ? '0 0 0 -20px' : '0'}
            minWidth={isMobile ? '100vw' : '495px'}
            padding='20px 20px 0px 20px'
            position='relative'
          >
            <Button
              borderRadius={getGlobalStyle('--border-radius-lg')}
              fontWeight={getGlobalStyle('--font-weight-semibold')}
              label='Confirmar dirección'
              onClick={confirmUserAddress}
              size={isMobile ? 'sm' : 'medium'}
              status={confirmAddressButtonStatus}
              width='100%'
            />
            <Spacer.Horizontal size={12} />
            <Button
              borderRadius='24px'
              fontWeight={getGlobalStyle('--font-weight-semibold')}
              label='Cambiar a retiro en tienda'
              onClick={changeDeliveryMethod}
              size={isMobile ? 'sm' : 'medium'}
              type='secondary'
              width='100%'
            />
          </Column>
        </>
      }
    </>
  )
}
