import { useCallback } from 'react'
import { AddressModal, MapModal } from './ModalState'
import { FailModal } from '../FailModal'
import { TitleProps } from '../../atoms/Title/Title'
import { UserAddressListProps } from './ModalState/AddressModal/AddressModalContent'
import { Site } from '../../../shared/interfaces/Site'
import { ButtonStatus } from '../../atoms'
import { SimulationItem, SimulationModal } from '../SimulationModal/SimulationModal'

export interface AddressListProps {
  activeMap: boolean;
  city: string;
  description: string;
  failModal: boolean;
  name: string;
}

export type SLButtonStatus = 'initial' | 'loading' | 'disabled'

export interface StoreLocatorProps {
  addressList?: AddressListProps[];
  buttonStatus: SLButtonStatus;
  children?: React.ReactNode;
  city?: string;
  commune?: string;
  comunaList?: string[];
  confirmAddressButtonStatus: ButtonStatus;
  contentPosition?: TitleProps['textAlign'];
  currentAddress?: UserAddressListProps;
  directionModal: boolean;
  errorAddress: boolean;
  errorAddressType: string;
  errorMapMessage: string;
  failModal: boolean;
  failModalButtonText: string;
  failModalColorTitle: string;
  failModalContent: string;
  simulationItems?: SimulationItem[];
  simulationUserAddress?: boolean;
  failModalTitle: string;
  manualAddress?: boolean;
  mapModal: boolean;
  placeholderSearch?: string;
  regionList?: string[];
  selectedAddress: string;
  selectedManualAddres: boolean;
  site?: Site;
  streetNumberInput: boolean;
  titleModal?: string;
  titlePosition?: TitleProps['textAlign'];
  userAddressList?: UserAddressListProps[];
  valueAddressComments: string;
  valueAddressRef: string;
  valueAddressStreetNumber: string;
  valueManualAddress?: string;
  valueSearch: string;
  addressOnChangeSearch: (e?: React.ChangeEvent<HTMLInputElement>) => void;
  addressOnClearInput: (e?: React.ChangeEvent<HTMLInputElement>, refAdornm?: unknown) => void;
  addressSelector: (data: object, index: number) => void;
  buttonAddressMap: (withSimulation?: boolean) => void;
  changeDeliveryMethod: () => void;
  confirmUserAddress: (withSimulation?: boolean) => void;
  deleteAddress: (data: object, index: number) => void;
  errorModal: () => void;
  failModalEvent: () => void;
  handleKeepAddress?: () => void;
  handleOnClick: () => void;
  handleSelectManualAddress?: () => void;
  mapAddressCommentsOnChangeSearch: (e?: React.ChangeEvent<HTMLInputElement>) => void;
  mapAddressCommentsOnClearInput: (e?: React.ChangeEvent<HTMLInputElement>, refAdornm?: unknown) => void;
  mapAddressOnChangeSearch: (e?: React.ChangeEvent<HTMLInputElement>) => void;
  mapAddressOnClearInput: (e?: React.ChangeEvent<HTMLInputElement>, refAdornm?: unknown) => void;
  mapAddressRefOnChangeSearch: (e?: React.ChangeEvent<HTMLInputElement>) => void;
  mapAddressRefOnClearInput: (e?: React.ChangeEvent<HTMLInputElement>, refAdornm?: unknown) => void;
  mapAddressStreetNumberOnChangeSearch: (e?: React.ChangeEvent<HTMLInputElement>) => void;
  mapAddressStreetNumberOnClearInput: (e?: React.ChangeEvent<HTMLInputElement>, refAdornm?: unknown) => void;
  mapBackModal: () => void;
  mapCloseModal: () => void;
  markingConfirmButton?: () => void;
  regionSelector?: (e?: React.ChangeEvent<HTMLSelectElement>) => void;
  validateAddress: (data: object) => Promise<{error: boolean}> | {error: boolean};
}

export const StoreLocator = ({
  addressList,
  buttonStatus,
  children,
  comunaList,
  confirmAddressButtonStatus,
  contentPosition,
  directionModal,
  errorAddress,
  errorAddressType,
  errorMapMessage,
  failModal,
  failModalButtonText,
  failModalColorTitle,
  failModalContent,
  simulationItems,
  simulationUserAddress = false,
  failModalTitle,
  manualAddress,
  mapModal,
  regionList,
  selectedAddress,
  selectedManualAddres,
  streetNumberInput,
  titlePosition,
  userAddressList,
  valueAddressComments,
  valueAddressRef,
  valueAddressStreetNumber,
  valueManualAddress,
  valueSearch,
  addressOnChangeSearch,
  addressOnClearInput,
  addressSelector,
  buttonAddressMap,
  changeDeliveryMethod,
  confirmUserAddress,
  deleteAddress,
  errorModal,
  failModalEvent,
  handleKeepAddress,
  handleOnClick,
  handleSelectManualAddress,
  mapAddressCommentsOnChangeSearch,
  mapAddressCommentsOnClearInput,
  mapAddressOnChangeSearch,
  mapAddressOnClearInput,
  mapAddressRefOnChangeSearch,
  mapAddressRefOnClearInput,
  mapAddressStreetNumberOnChangeSearch,
  mapAddressStreetNumberOnClearInput,
  mapBackModal,
  mapCloseModal,
  markingConfirmButton,
  regionSelector,
  validateAddress
}: StoreLocatorProps): React.ReactElement => {
  const simulationModalAction = useCallback(() => {
    return simulationUserAddress ? confirmUserAddress(false) : buttonAddressMap(false)
  }, [simulationUserAddress])

  return (
    <>
      {
        directionModal &&
        <AddressModal
          addressList={addressList}
          addressOnChangeSearch={addressOnChangeSearch}
          addressOnClearInput={addressOnClearInput}
          addressSelector={addressSelector}
          changeDeliveryMethod={changeDeliveryMethod}
          confirmAddressButtonStatus={confirmAddressButtonStatus}
          confirmUserAddress={confirmUserAddress}
          deleteAddress={deleteAddress}
          manualAddress={manualAddress}
          onClick={handleOnClick}
          onSelect={validateAddress}
          onSelectManualAddress={handleSelectManualAddress}
          userAddressList={userAddressList}
          value={valueSearch}
        />
      }
      {simulationItems?.length > 0 &&
        <SimulationModal
          action={simulationModalAction}
          buttonStatus={buttonStatus}
          handleKeepAddress={handleKeepAddress}
          items={simulationItems}
          labelPrimary='No, dejar la actual'
          labelSecondary='Si, quiero cambiarla'
        />
      }
      {
        failModal &&
        <FailModal
          action={failModalEvent}
          buttonText={failModalButtonText}
          colorTitle={failModalColorTitle}
          content={failModalContent}
          contentPosition={contentPosition}
          handleKeepAddress={handleKeepAddress}
          labelSecondary='Mantener mi dirección'
          onClick={errorModal}
          title={failModalTitle}
          titlePosition={titlePosition}
        />
      }
      {
        mapModal &&
        <MapModal
          back={mapBackModal}
          buttonAddressMap={buttonAddressMap}
          buttonStatus={buttonStatus}
          changeDeliveryMethod={changeDeliveryMethod}
          comunaList={comunaList}
          errorAddress={errorAddress}
          errorAddressType={errorAddressType}
          errorMapMessage={errorMapMessage}
          mapAddressCommentsOnChangeSearch={mapAddressCommentsOnChangeSearch}
          mapAddressCommentsOnClearInput={mapAddressCommentsOnClearInput}
          mapAddressOnChangeSearch={mapAddressOnChangeSearch}
          mapAddressOnClearInput={mapAddressOnClearInput}
          mapAddressRefOnChangeSearch={mapAddressRefOnChangeSearch}
          mapAddressRefOnClearInput={mapAddressRefOnClearInput}
          mapAddressStreetNumberOnChangeSearch={mapAddressStreetNumberOnChangeSearch}
          mapAddressStreetNumberOnClearInput={mapAddressStreetNumberOnClearInput}
          markingConfirmButton={markingConfirmButton}
          onClick={mapCloseModal}
          regionList={regionList}
          regionSelector={regionSelector}
          selectedAddress={selectedAddress}
          selectedManualAddres={selectedManualAddres}
          streetNumberInput={streetNumberInput}
          validateAddress={validateAddress}
          valueAddressComments={valueAddressComments}
          valueAddressRef={valueAddressRef}
          valueAddressStreetNumber={valueAddressStreetNumber}
          valueManualAddress={valueManualAddress}
        >
          {children}
        </MapModal>
      }
    </>
  )
}