import { Ref } from 'react'
import {
  Text,
  Icon,
  Divider
} from '../../../../../atoms'
import { InputAdornment } from '../../../../../molecules'
import {
  Horizontal,
  Row,
  Column
} from '../../../../../layout'
import { getGlobalStyle } from '../../../../../../helpers'

interface MapModalProps {
  defaultValue?: string;
  focusedInput: string;
  inputRef: Ref<unknown>;
  inputTitle: string;
  inputValue: string;
  placeholder: string;
  type?: string;
  clearIcon: (e?: React.ChangeEvent<HTMLInputElement>, refAdornm?: unknown) => void;
  onChange: (e?: React.ChangeEvent<HTMLInputElement>) => void;
  setFocusedInput: (e?: string) => void;
}

export const RenderInputField = ({
  defaultValue,
  focusedInput,
  inputRef,
  inputTitle,
  inputValue,
  placeholder,
  type,
  clearIcon,
  onChange,
  setFocusedInput
}: MapModalProps): React.ReactElement => {
  const inputIsFocused = focusedInput === inputTitle

  return (
    <Column>
      <Row>
        <Text fontSize='md' fontWeight='semibold'>
          {inputTitle}
        </Text>
      </Row>
      <Horizontal size={8} />
      <Row>
        <>
          <InputAdornment
            defaultValue={defaultValue}
            fontSize='base'
            height='20px'
            inputAdornmentEnd={
              inputValue && (
                <Icon
                  clickable='pointer'
                  color={getGlobalStyle('--color-neutral-gray-dark')}
                  customSize={inputIsFocused ? 14 : 20}
                  name={inputIsFocused ? 'Close' : 'Edit'}
                  onClick={inputIsFocused ? clearIcon : undefined}
                />
              )
            }
            inputWidth='100%'
            onChange={onChange}
            onFocus={() => {setFocusedInput(inputTitle)}}
            placeholder={placeholder}
            placeholderFocus='placeholder'
            ref={inputRef}
            type={type}
            width='auto'
          />
        </>
      </Row>
      <Divider
        color={getGlobalStyle('--color-neutral-gray')}
        thickness={1}
        verticalMargin={6}
      />
    </Column>
  )
}