import React, {
  useEffect,
  useRef,
  useState
} from 'react'
import {
  Text,
  Divider,
  Button
} from '../../../../../atoms'
import {
  Column,
  Container,
  Row,
  Spacer,
  Vertical
} from '../../../../../layout'
import styles from '../../MapModal/index.module.css'
import { getGlobalStyle } from '../../../../../../helpers'
import { SLButtonStatus } from '../../../StoreLocator'
import { InputRender } from './InputRender'

interface MapModalProps {
  buttonStatus: SLButtonStatus;
  isMobile?: boolean;
  selectedAddress: string;
  streetNumberInput: boolean;
  valueAddressComments: string;
  valueAddressRef: string;
  valueAddressStreetNumber: string;
  buttonAddressMap: () => void;
  mapAddressCommentsOnChangeSearch: (e?: React.ChangeEvent<HTMLInputElement>) => void;
  mapAddressCommentsOnClearInput: (e?: React.ChangeEvent<HTMLInputElement>, refAdornm?: unknown) => void;
  mapAddressRefOnChangeSearch: (e?: React.ChangeEvent<HTMLInputElement>) => void;
  mapAddressRefOnClearInput: (e?: React.ChangeEvent<HTMLInputElement>, refAdornm?: unknown) => void;
  mapAddressStreetNumberOnChangeSearch: (e?: React.ChangeEvent<HTMLInputElement>) => void;
  mapAddressStreetNumberOnClearInput: (e?: React.ChangeEvent<HTMLInputElement>, refAdornm?: unknown) => void;
}

interface FocusedProps {
  streetNumber: boolean;
  addressRef: boolean;
  comments: boolean;
}

export const MapModalContent = ({
  buttonStatus,
  isMobile,
  selectedAddress,
  streetNumberInput,
  valueAddressComments,
  valueAddressRef,
  valueAddressStreetNumber,
  buttonAddressMap,
  mapAddressCommentsOnChangeSearch,
  mapAddressCommentsOnClearInput,
  mapAddressRefOnChangeSearch,
  mapAddressRefOnClearInput,
  mapAddressStreetNumberOnChangeSearch,
  mapAddressStreetNumberOnClearInput
}: MapModalProps): React.ReactElement => {
  const inputAdornmentRefAddress = useRef<HTMLDivElement>(null)
  const inputAdornmentRefComment = useRef<HTMLDivElement>(null)
  const inputAdornmentRefStreetNumber = useRef<HTMLDivElement>(null)
  const [inputWarning, setInputWarning] = useState(false)
  const [focused, setFocused] = useState<FocusedProps>({
    streetNumber: false,
    addressRef: false,
    comments: false
  })
  const inputRenderProps = {
    focused,
    inputWarning,
    setFocused
  }
  const onClickClearInputAddressRef = (e?: React.ChangeEvent<HTMLInputElement>) => {
    mapAddressRefOnClearInput(e, inputAdornmentRefAddress)
  }
  const onClickClearInputAddressComment = (e?: React.ChangeEvent<HTMLInputElement>) => {
    mapAddressCommentsOnClearInput(e, inputAdornmentRefComment)
  }
  const onClickClearInputStreetNumber = (e?: React.ChangeEvent<HTMLInputElement>) => {
    mapAddressStreetNumberOnClearInput(e, inputAdornmentRefStreetNumber)
  }

  const handleInputScroll = () => {
    const activeElement = document?.activeElement as HTMLInputElement
    if (activeElement) {
      if (activeElement?.type === 'number') {
        activeElement.blur()
      }
    }
  }

  useEffect(() => {
    window.addEventListener('wheel', handleInputScroll)
    return () => {
      window.removeEventListener('wheel', handleInputScroll)
    }
  }, [])

  return (
    <>
      <Spacer.Horizontal size={8} />
      <Column customHeight={isMobile ? null : '125px'} overflow='auto'>
        <Container margin='10px 0px'>
          <Row>
            <Column customWidth='auto' justifyContent='center'>
              <Row margin='0 0 10px 0'>
                <Text fontSize='md' fontWeight='semibold'>
                  Dirección
                </Text>
              </Row>
              <Row justifyContent='between'>
                <Text customClassName={styles.ellipsisText} fontSize='lg'>
                  {selectedAddress}
                </Text>
                <Vertical size={8} />
              </Row>
            </Column>
          </Row>
        </Container>
        <Container>
          <Row>
            <Column>
              <Spacer.Horizontal size={12} />
              {streetNumberInput &&
                <Column>
                  <Row>
                    <Text fontSize='md' fontWeight='semibold'>
                      Número de la calle
                    </Text>
                  </Row>
                  <Spacer.Horizontal size={8} />
                  <InputRender
                    clearInput={(inputData) => {
                      setInputWarning(true)
                      return onClickClearInputStreetNumber(inputData)
                    }}
                    inputRef={inputAdornmentRefStreetNumber}
                    inputValue={valueAddressStreetNumber}
                    onChange={(inputData) => {
                      setInputWarning(!inputData.target.value)
                      return mapAddressStreetNumberOnChangeSearch(inputData)
                    }}
                    placeholder={'Ingresa el número de la calle'}
                    type={'streetNumber'}
                    {...inputRenderProps}
                  />
                  <Divider
                    color={inputWarning ? getGlobalStyle('--color-text-error') : ''}
                    thickness={1}
                    verticalMargin={6}
                  />
                  {inputWarning ? (
                    <>
                      <Text color={'error'} fontSize='md'>
                        Ingresa un número válido
                      </Text>
                      <Spacer.Horizontal size={4} />
                    </>
                  ) : (
                    <Spacer.Horizontal size={18} />
                  )}
                </Column>
              }
              <Column>
                <Row>
                  <Text fontSize='md' fontWeight='semibold'>
                    Departamento/Casa/Piso (opcional)
                  </Text>
                </Row>
                <Spacer.Horizontal size={8} />
                <InputRender
                  clearInput={onClickClearInputAddressRef}
                  inputRef={inputAdornmentRefAddress}
                  inputValue={valueAddressRef}
                  onChange={mapAddressRefOnChangeSearch}
                  placeholder={'Ingresa el departamento / casa / piso'}
                  type={'addressRef'}
                  {...inputRenderProps}
                />
              </Column>
              <Divider thickness={1} verticalMargin={6} />
              <Spacer.Horizontal size={18} />
              <Column>
                <Row>
                  <Text fontSize='md' fontWeight='semibold'>
                    Instrucciones y comentarios (opcional)
                  </Text>
                </Row>
                <Spacer.Horizontal size={8} />
                <InputRender
                  clearInput={onClickClearInputAddressComment}
                  inputRef={inputAdornmentRefComment}
                  inputValue={valueAddressComments}
                  onChange={mapAddressCommentsOnChangeSearch}
                  placeholder={'Ingresa alguna instrucciones y/o comentario'}
                  type={'comments'}
                  {...inputRenderProps}
                />
              </Column>
              <Divider thickness={1} verticalMargin={6} />
            </Column>
          </Row>
        </Container>
      </Column>
      <Spacer.Horizontal size={18} />
      <Column
        className={styles.containerShadow}
        margin={isMobile ? '0 0 0 -20px' : '0'}
        minWidth={isMobile ? '100vw' : '495px'}
        padding='20px 20px 0px 20px'
        position='relative'
      >
        <Button
          borderRadius={getGlobalStyle('--border-radius-xl')}
          fontWeight={getGlobalStyle('--font-weight-semibold')}
          label='Guardar dirección'
          onClick={buttonAddressMap}
          size='large'
          status={buttonStatus}
          width='100%'
        />
      </Column>
    </>
  )
}