import React, {
  useEffect,
  useRef,
  useState
} from 'react'
import { Text, Button } from '../../../../../atoms'
import { Selector } from '../../../../../molecules'
import {
  Column,
  Container,
  Spacer
} from '../../../../../layout'
import { FieldValue } from './FieldValue'
import { RenderInputField } from './RenderInputField'
import { getGlobalStyle } from '../../../../../../helpers'
import styles from '../../MapModal/index.module.css'
import { MapModalProps } from '..'

interface ManualAddressFormProps extends MapModalProps {
  isMobile?: boolean;
  step2ManualAddress?: boolean;
  valueAddressStreetNumber?: string;
  setStep2ManualAddress: (props: boolean) => void;
}

export const ManualAddressForm = ({
  buttonStatus,
  comunaList,
  isMobile = false,
  regionList,
  step2ManualAddress,
  valueAddressComments,
  valueAddressStreetNumber,
  valueAddressRef,
  valueManualAddress,
  buttonAddressMap,
  changeDeliveryMethod,
  mapAddressCommentsOnChangeSearch,
  mapAddressCommentsOnClearInput,
  mapAddressOnChangeSearch,
  mapAddressOnClearInput,
  mapAddressRefOnChangeSearch,
  mapAddressRefOnClearInput,
  mapAddressStreetNumberOnChangeSearch,
  mapAddressStreetNumberOnClearInput,
  markingConfirmButton,
  regionSelector,
  setStep2ManualAddress,
  validateAddress
}: ManualAddressFormProps): React.ReactElement => {
  const [focusedInput, setFocusedInput] = useState('')
  const [communeVal, setCommuneVal] = useState('')
  const [apartment, setApartment] = useState('')
  const [regionVal, setRegionVal] = useState('')
  const [loadingBtn, setLoadingBtn] = useState(false)
  const inputAdornmentRefAddress = useRef<HTMLDivElement>(null)
  const inputAdornmentAddress = useRef<HTMLDivElement>(null)
  const inputAdornmentStreetNumber = useRef<HTMLDivElement>(null)
  const inputAdornmentRefComment = useRef<HTMLDivElement>(null)

  const onClickClearInputAddressRef = (e?: React.ChangeEvent<HTMLInputElement>) => {
    mapAddressRefOnClearInput(e, inputAdornmentRefAddress)
  }
  const onClickClearInputAddress = (e?: React.ChangeEvent<HTMLInputElement>) => {
    mapAddressOnClearInput(e, inputAdornmentAddress)
  }
  const onClickClearInputAddressComment = (e?: React.ChangeEvent<HTMLInputElement>) => {
    mapAddressCommentsOnClearInput(e, inputAdornmentRefComment)
  }
  const onClickClearInputStreetNumber = (e?: React.ChangeEvent<HTMLInputElement>) => {
    mapAddressStreetNumberOnClearInput(e, inputAdornmentStreetNumber)
  }

  const onClickConfirmBtn = async () => {
    setLoadingBtn(true)
    // validate if markingConfirmButton is a function
    if (typeof markingConfirmButton === 'function') {
      markingConfirmButton()
    }
    const addressResult = await validateAddress({
      description: `${valueManualAddress}, ${communeVal}, Chile`,
      refreshMap: false
    })
    if (addressResult.error) return

    setLoadingBtn(false)
    setStep2ManualAddress(!step2ManualAddress)
  }

  const btnStatus = () => {
    if (loadingBtn) return 'loading'

    return (
      communeVal.includes('Selecciona') || regionVal.includes('Seleccionar') ? 'disabled' : buttonStatus
    )
  }

  const handleInputScroll = () => {
    const activeElement = document?.activeElement as HTMLInputElement
    if (activeElement) {
      if (activeElement?.type === 'number') {
        activeElement.blur()
      }
    }
  }

  useEffect(() => {
    window.addEventListener('wheel', handleInputScroll)
    return () => {
      window.removeEventListener('wheel', handleInputScroll)
    }
  }, [])

  if (step2ManualAddress) return (
    <>
      <Spacer.Horizontal size={16} />
      <Container customHeight={isMobile ? null : '135px'} overflow='auto'>
        <Column maxWidth={isMobile ? '98%' : '100%'}>
          <FieldValue fieldTitle='Dirección' fieldValue={valueManualAddress} />
          <FieldValue fieldTitle='Región' fieldValue={regionVal} />
          <FieldValue fieldTitle='Comuna' fieldValue={communeVal} />
          <RenderInputField
            clearIcon={onClickClearInputAddressRef}
            defaultValue={apartment}
            focusedInput={focusedInput}
            inputRef={inputAdornmentRefAddress}
            inputTitle='Departamento, casa u oficina'
            inputValue={valueAddressRef}
            onChange={mapAddressRefOnChangeSearch}
            placeholder='Ingresa tu departamento/oficina/casa/piso'
            setFocusedInput={setFocusedInput}
          />
          <Spacer.Horizontal size={18} />
          <RenderInputField
            clearIcon={onClickClearInputAddressComment}
            defaultValue={apartment}
            focusedInput={focusedInput}
            inputRef={inputAdornmentRefComment}
            inputTitle='Instrucciones adicionales'
            inputValue={valueAddressComments}
            onChange={mapAddressCommentsOnChangeSearch}
            placeholder='Agrega información adicional'
            setFocusedInput={setFocusedInput}
          />
          <Spacer.Horizontal size={18} />
        </Column>
      </Container>
      <Column
        className={styles.containerShadow}
        margin={isMobile ? '0 0 0 -20px' : '0'}
        minWidth={isMobile ? '100vw' : '495px'}
        padding='20px 20px 0px 20px'
        position='relative'
      >
        <Button
          borderRadius={getGlobalStyle('--border-radius-xl')}
          fontWeight={getGlobalStyle('--font-weight-semibold')}
          label='Guardar dirección'
          onClick={buttonAddressMap}
          size='large'
          status={buttonStatus}
          width='100%'
        />
      </Column>
    </>
  )

  return (
    <>
      <Column customHeight={isMobile ? null : '315px'} overflow='auto'>
        <Column>
          <Text fontSize='md' fontWeight='semibold'>
            Región
          </Text>
          <Selector
            appearance='none'
            arrow
            color='black'
            iconName='DropDownCloseV2'
            iconSize={20}
            marginLess
            onChange={(e) => {
              if (!e.target.value) return

              regionSelector(e)
              setRegionVal(e.target.value)
            }}
            onFocus={() => { setFocusedInput('') }}
            options={regionList}
            padding='8px 0px 6px 0px'
            placeholder='Selecciona tu región'
            ref={(ref) => {
              if (!ref?.value) return

              setRegionVal(ref.value)
            }}
          />
          <Spacer.Horizontal size={18} />
          <Text fontSize='md' fontWeight='semibold'>
            Comuna
          </Text>
          <Selector
            appearance='none'
            arrow
            color='black'
            iconName='DropDownCloseV2'
            iconSize={20}
            marginLess
            onChange={(e) => {
              if (!e.target.value) return

              setCommuneVal(e.target.value)
            }}
            onFocus={() => { setFocusedInput('') }}
            options={comunaList}
            padding='8px 0px 6px 0px'
            placeholder='Selecciona tu comuna'
            ref={(ref) => {
              if (!ref?.value) return

              setCommuneVal(ref.value)
            }}
          />
          <Spacer.Horizontal size={18} />
          <RenderInputField
            clearIcon={onClickClearInputAddress}
            focusedInput={focusedInput}
            inputRef={inputAdornmentAddress}
            inputTitle='Ingresa nombre de tu calle'
            inputValue={valueManualAddress}
            onChange={mapAddressOnChangeSearch}
            placeholder='Ingresa nombre de tu dirección'
            setFocusedInput={setFocusedInput}
          />
          <Spacer.Horizontal size={18} />
          <RenderInputField
            clearIcon={onClickClearInputStreetNumber}
            focusedInput={focusedInput}
            inputRef={inputAdornmentStreetNumber}
            inputTitle='Número de calle'
            inputValue={valueAddressStreetNumber}
            onChange={mapAddressStreetNumberOnChangeSearch}
            placeholder='Ingresa número de tu dirección'
            setFocusedInput={setFocusedInput}
            type='number'
          />

          <Spacer.Horizontal size={18} />
          <RenderInputField
            clearIcon={onClickClearInputAddressRef}
            focusedInput={focusedInput}
            inputRef={inputAdornmentRefAddress}
            inputTitle='Departamento/Casa/Piso (opcional)'
            inputValue={valueAddressRef}
            onChange={(e) => {
              setApartment(e.target.value)
              mapAddressRefOnChangeSearch(e)
            }}
            placeholder='Ingresa tu departamento/casa/piso'
            setFocusedInput={setFocusedInput}
          />
        </Column>
      </Column>
      <Column
        className={styles.containerShadow}
        margin={isMobile ? '0 0 0 -20px' : '0'}
        minWidth={isMobile ? '100vw' : '495px'}
        padding='20px 20px 0px 20px'
        position='relative'
      >
        <Column>
          <Button
            borderRadius={getGlobalStyle('--border-radius-xl')}
            fontWeight={getGlobalStyle('--font-weight-semibold')}
            label='Confirmar dirección'
            onClick={onClickConfirmBtn}
            size='large'
            status={btnStatus()}
            width='100%'
          />
          <Spacer.Horizontal size={12} />
          <Button
            borderRadius={getGlobalStyle('--border-radius-xl')}
            fontWeight={getGlobalStyle('--font-weight-semibold')}
            label='Cambiar a retiro en tienda'
            onClick={changeDeliveryMethod}
            size='large'
            type='secondary'
            width='100%'
          />
        </Column>
      </Column>
    </>
  )
}