import React, { Ref } from 'react'
import { Icon } from '../../../../../atoms'
import { InputAdornment } from '../../../../../molecules'
import { Row } from '../../../../../layout'
import { getGlobalStyle } from '../../../../../../helpers'

interface InputRenderProps {
  focused: object;
  inputRef: Ref<unknown>;
  inputValue: string;
  inputWarning: boolean
  placeholder: string;
  type: string;
  clearInput: (e?: React.ChangeEvent<HTMLInputElement>) => void;
  onChange: (e?: React.ChangeEvent<HTMLInputElement>) => void;
  setFocused: (props?: object) => void;
}

export const InputRender = ({
  focused,
  inputRef,
  inputValue,
  inputWarning,
  placeholder,
  type,
  clearInput,
  onChange,
  setFocused
}: InputRenderProps): React.ReactElement => {
  const isStreetNumber = type === 'streetNumber'
  const handleFocusInputs = (objectValTrue: string) => {
    const newFocusedObject = {...focused}
    Object.keys(newFocusedObject).forEach(key => {
      if (key !== 'streetNumber') {
        newFocusedObject[key] = key == objectValTrue
      } else {
        newFocusedObject[key] = inputWarning || key == objectValTrue
      }
    })
    setFocused(newFocusedObject)
  }

  return (
    <Row>
      <InputAdornment
        fontSize='base'
        height='20px'
        inputAdornmentEnd={
          (inputValue || (isStreetNumber && inputWarning)) && (
            <Icon
              clickable='pointer'
              color={(isStreetNumber && inputWarning) ? getGlobalStyle('--color-text-error') : getGlobalStyle('--color-neutral-gray-dark')}
              customSize={focused[type] ? 12 : 20}
              name={focused[type] ? 'Close' : 'Edit'}
              onClick={focused[type] ? clearInput : undefined}
            />
          )
        }
        inputWidth='100%'
        onChange={onChange}
        onFocus={() => {return handleFocusInputs(type)}}
        placeholder={placeholder}
        placeholderFocus='placeholder'
        ref={inputRef}
        type={isStreetNumber && 'number'}
        width='auto'
      />
    </Row>
  )
}