import { useState, useEffect} from 'react'
import { Modal } from '../../../../molecules'
import { Column, Spacer } from '../../../../layout'
import { BigScreen, SmallScreen } from '../../../../../helpers/mediaQueries'
import { AddressModalContent, UserAddressListProps } from '../AddressModal/AddressModalContent'
import { ButtonStatus } from '../../../../atoms'
import { AddressListProps } from '../../StoreLocator'

export interface AddressModalProps {
  addressList?: AddressListProps[];
  confirmAddressButtonStatus?: ButtonStatus;
  currentAddress?: UserAddressListProps;
  isOpen?: boolean;
  manualAddress?: boolean;
  placeholderSearch?: string;
  placeholderSearchPickUp?: string;
  site?: 'unimarc' | 'alvi';
  userAddressList?: UserAddressListProps[];
  value?: string;
  addressOnChangeSearch?: (e?: React.ChangeEvent<HTMLInputElement>) => void;
  addressOnClearInput?: (e?: React.ChangeEvent<HTMLInputElement>, refAdornm?: unknown) => void;
  addressSelector?: (data: object, index: number) => void;
  changeDeliveryMethod?: () => void;
  confirmUserAddress?: () => void;
  deleteAddress?: (data: object, index: number) => void;
  onClick?: () => void;
  onSelect?: (data: object) => void;
  onSelectManualAddress?: () => void;
}

export const AddressModal = ({
  addressList,
  confirmAddressButtonStatus,
  isOpen = true,
  manualAddress,
  userAddressList,
  value,
  addressOnChangeSearch,
  addressOnClearInput,
  addressSelector,
  changeDeliveryMethod,
  confirmUserAddress,
  deleteAddress,
  onClick,
  onSelect,
  onSelectManualAddress
}: AddressModalProps): React.ReactElement => {
  const [screenHeight, setScreenHeight] = useState(`${(window.innerHeight * 0.94).toString()}px`)

  useEffect(() => {
    const resizeEvent = () => {
      setScreenHeight(`${(window.innerHeight * 0.94).toString()}px`)
    }
    window.addEventListener('resize', resizeEvent)
    return () => {
      window.removeEventListener('resize', resizeEvent)
    }
  }, [window.innerHeight])

  const AddressPropsUnimarc = {
    addressList,
    confirmAddressButtonStatus,
    manualAddress,
    userAddressList,
    value,
    addressOnChangeSearch,
    addressOnClearInput,
    addressSelector,
    changeDeliveryMethod,
    confirmUserAddress,
    deleteAddress,
    onClick,
    onSelect,
    onSelectManualAddress
  }
  return (
    <>
      <BigScreen>
        <Modal
          hiddenX
          isOpen={isOpen}
          maxHeight='550px'
          maxWidth='495px'
          minHeight='550px'
          toggle={null}
          toggleOutside={null}
        >
          <Spacer.Horizontal size={8} />
          <AddressModalContent {...AddressPropsUnimarc} />
        </Modal>
      </BigScreen>
      <SmallScreen>
        <Modal
          fullScreen
          hiddenX
          isOpen={isOpen}
          marginFullScreen='1vh 0 0 0'
          minHeightFullScreen={screenHeight}
          toggle={onClick}
          toggleOutside={onClick}
        >
          <Column customHeight={screenHeight}>
            <Spacer.Horizontal size={8} />
            <AddressModalContent {...AddressPropsUnimarc} isMobile/>
          </Column>
        </Modal>
      </SmallScreen>
    </>
  )
}
