import { useEffect, useState } from 'react'
import { Text } from '../../../../atoms'
import { Modal } from '../../../../molecules'
import {
  Row,
  Column,
  Spacer
} from '../../../../layout'
import { BigScreen, SmallScreen } from '../../../../../helpers/mediaQueries'
import { MapModalContent } from '../MapModal/MapModalContent'
import { ErrorMapMessageProps } from '../../../StoreLocatorAlvi/ModalState/MapModal/helpers/ErrorMapMessageProps'
import { ManualAddressForm } from './ManualAddressForm'
import { ModalHeader } from './ModalHeader'
import { SLButtonStatus } from '../../StoreLocator'

export interface MapModalProps {
  buttonStatus?: SLButtonStatus;
  children?: React.ReactNode;
  city?: string;
  commune?: string;
  comunaList?: string[];
  errorAddress?: boolean;
  errorAddressType?: string;
  errorMapMessage?: string;
  isOpen?: boolean;
  regionList?: string[];
  selectedAddress?: string;
  selectedManualAddres?: boolean;
  site?: 'unimarc' | 'alvi';
  streetNumberInput?: boolean;
  titleModal?: string;
  valueAddressComments: string;
  valueAddressRef?: string;
  valueAddressStreetNumber?: string;
  valueManualAddress?: string;
  back?: () => void;
  buttonAddressMap: () => void;
  changeDeliveryMethod: () => void;
  mapAddressCommentsOnChangeSearch: (e?: React.ChangeEvent<HTMLInputElement>) => void;
  mapAddressCommentsOnClearInput: (e?: React.ChangeEvent<HTMLInputElement>, refAdornm?: unknown) => void;
  mapAddressOnChangeSearch: (e?: React.ChangeEvent<HTMLInputElement>) => void;
  mapAddressOnClearInput: (e?: React.ChangeEvent<HTMLInputElement>, refAdornm?: unknown) => void;
  mapAddressRefOnChangeSearch: (e?: React.ChangeEvent<HTMLInputElement>) => void;
  mapAddressRefOnClearInput: (e?: React.ChangeEvent<HTMLInputElement>, refAdornm?: unknown) => void;
  mapAddressStreetNumberOnChangeSearch: (e?: React.ChangeEvent<HTMLInputElement>) => void;
  mapAddressStreetNumberOnClearInput: (e?: React.ChangeEvent<HTMLInputElement>, refAdornm?: unknown) => void;
  markingConfirmButton?: () => void;
  onClick?: () => void;
  regionSelector?: (e?: React.ChangeEvent<HTMLSelectElement>) => void;
  validateAddress: (data: object) => Promise<{error: boolean}> | {error: boolean};
}

const MapMakerText = () => {
  return (
    <Column alignItems='end' margin='8px 0 0 0'>
      <Text color='gray' fontSize='md'>*Ajusta el marcador para ubicar la dirección.</Text>
    </Column>
  )
}

export const MapModal = ({
  buttonStatus,
  children,
  comunaList,
  errorAddress,
  errorAddressType,
  errorMapMessage,
  isOpen = true,
  regionList,
  selectedAddress,
  selectedManualAddres = false,
  streetNumberInput,
  valueAddressComments,
  valueAddressRef,
  valueAddressStreetNumber,
  valueManualAddress,
  back,
  changeDeliveryMethod,
  buttonAddressMap,
  mapAddressCommentsOnChangeSearch,
  mapAddressCommentsOnClearInput,
  mapAddressOnChangeSearch,
  mapAddressOnClearInput,
  mapAddressRefOnChangeSearch,
  mapAddressRefOnClearInput,
  mapAddressStreetNumberOnChangeSearch,
  mapAddressStreetNumberOnClearInput,
  markingConfirmButton,
  onClick,
  regionSelector,
  validateAddress
}: MapModalProps): React.ReactElement => {
  const [step2ManualAddress, setStep2ManualAddress] = useState<boolean>(false)
  const [screenHeight, setScreenHeight] = useState(`${(window.innerHeight * 0.94).toString()}px`)

  useEffect(() => {
    const resizeEvent = () => {
      setScreenHeight(`${(window.innerHeight * 0.94).toString()}px`)
    }
    window.addEventListener('resize', resizeEvent)
    return () => {
      window.removeEventListener('resize', resizeEvent)
    }
  }, [window.innerHeight])

  const MapModalPropsGlobalUnimarc = {
    buttonStatus,
    selectedAddress,
    streetNumberInput,
    valueAddressComments,
    valueAddressRef,
    valueAddressStreetNumber,
    buttonAddressMap,
    mapAddressCommentsOnChangeSearch,
    mapAddressCommentsOnClearInput,
    mapAddressRefOnChangeSearch,
    mapAddressRefOnClearInput,
    mapAddressStreetNumberOnChangeSearch,
    mapAddressStreetNumberOnClearInput
  }
  const ManualAddressProps = {
    buttonStatus,
    comunaList,
    regionList,
    step2ManualAddress,
    valueAddressComments,
    valueAddressRef,
    valueAddressStreetNumber,
    valueManualAddress,
    buttonAddressMap,
    changeDeliveryMethod: () => {
      back()
      changeDeliveryMethod()
    },
    mapAddressCommentsOnChangeSearch,
    mapAddressCommentsOnClearInput,
    mapAddressOnChangeSearch,
    mapAddressOnClearInput,
    mapAddressRefOnChangeSearch,
    mapAddressRefOnClearInput,
    mapAddressStreetNumberOnChangeSearch,
    mapAddressStreetNumberOnClearInput,
    markingConfirmButton,
    regionSelector,
    setStep2ManualAddress,
    validateAddress
  }
  const errorPropsMessageUnimarc = {
    errorAddress,
    errorAddressType,
    errorMapMessage
  }

  return (
    <>
      <BigScreen>
        <Modal
          hiddenX
          isOpen={isOpen}
          maxHeight='550px'
          maxWidth='495px'
          minHeight='550px'
          toggle={null}
          toggleOutside={null}
        >
          <Spacer.Horizontal size={8} />
          {selectedManualAddres ? (
            <>
              <ModalHeader back={back} title={step2ManualAddress ? 'Información de la dirección' : '¿Dónde quieres recibir tu compra?'}/>
              {step2ManualAddress && (
                <>
                  <Column alignItems='center' customWidth='495px'>
                    <Row minWidth='100%'>
                      {children}
                    </Row>
                    <ErrorMapMessageProps {...errorPropsMessageUnimarc} top='330'/>
                  </Column>
                  <MapMakerText/>
                </>
              )}
              <ManualAddressForm {...ManualAddressProps}/>
            </>
          ) : (
            <>
              <ModalHeader back={back}/>
              <Column alignItems='center' customWidth='495px'>
                <Row minWidth='100%'>
                  {children}
                </Row>
                <ErrorMapMessageProps {...errorPropsMessageUnimarc} top='330'/>
              </Column>
              <MapMakerText/>
              <MapModalContent {...MapModalPropsGlobalUnimarc} />
            </>
          )}
        </Modal>
      </BigScreen>

      <SmallScreen>
        <Modal
          fullScreen
          hiddenX
          isOpen={isOpen}
          marginFullScreen='1vh 0 0 0'
          minHeightFullScreen={screenHeight}
          toggle={onClick}
          toggleOutside={onClick}
        >
          <Column customHeight={screenHeight}>
            <ErrorMapMessageProps {...errorPropsMessageUnimarc}/>
            <Spacer.Horizontal size={8} />
            {selectedManualAddres ? (
              <>
                <ModalHeader back={back}/>
                {step2ManualAddress && (
                  <Column
                    alignItems='center'
                    customWidth='495px'
                    justifyContent='center'
                  >
                    <Row minWidth='100vw'>
                      {children}
                    </Row>
                  </Column>
                )}
                { step2ManualAddress && <MapMakerText/>}
                <Spacer.Horizontal size={8} />
                <ManualAddressForm {...ManualAddressProps} isMobile/>
              </>
            ) : (
              <>
                <ModalHeader back={back}/>
                <Column
                  alignItems='center'
                  customWidth='495px'
                  justifyContent='center'
                >
                  <Row minWidth='100vw'>
                    {children}
                  </Row>
                  <ErrorMapMessageProps {...errorPropsMessageUnimarc}/>
                </Column>
                <MapMakerText/>
                <Spacer.Horizontal size={16} />
                <MapModalContent {...MapModalPropsGlobalUnimarc} isMobile/>
              </>
            )}
          </Column>
        </Modal>
      </SmallScreen>
    </>
  )
}